/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton+SC&display=swap');

/* Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Global Styles */
body {
  font-family: "Anton SC", sans-serif; /* Changed to Anton SC for paragraph text */
  background-color: #121212;
  color: #eaeaea;
  overflow-x: hidden;
}

/* Container */
.container {
  max-width: 1400px; /* Default container width */
  margin: 0 auto;
  padding: 0 20px;
}

/* Feature Projects Section */
.feature-projects-container {
  max-width: 1200px; /* Increased width for feature projects */
}

/* Hero Section */
.hero {
  position: relative;
  height: 100vh; /* Set to full viewport height */
  background-image: url('/public/main.jpg'); /* Path to your image */
  background-size: cover; /* Ensures the image covers the entire section */
  background-position: center; /* Center the image */
  filter: brightness(0.6); /* Darken the image */
  display: flex; /* Center text inside the hero section */
  flex-direction: column; /* Stack title and subtitle vertically */
  justify-content: center; /* Center vertically */
  align-items: center; /* Center horizontally */
}

.hero-title,
.hero-subtitle {
  color: white; /* Adjust text color as needed */
}

.hero-title {
  font-family: "Righteous", sans-serif; /* Changed to Righteous for title */
  font-size: 3rem; /* Adjust size as necessary */
}

.hero-subtitle {
  font-family: "Anton SC", sans-serif; /* Changed to Anton SC for subtitle */
  font-size: 1.5rem; /* Adjust size as necessary */
}

/* Sections */
section {
  margin: 40px 0;
}

h2 {
  font-family: "Righteous", sans-serif; /* Changed to Righteous for headings */
  font-size: 2.8rem;
  margin-bottom: 20px;
}




/* About Section ----------------------------------------------------------------------------------------------- */
.about-section {
  padding: 80px 20px;
  background-color: #1a1a1a; /* Dark background for contrast */
  text-align: center; /* Center the section overall */
}

.about-title {
  font-family: "Righteous", sans-serif; /* Use Righteous for the title */
  font-size: 2.2rem; /* Adjust title size */
  margin: 0; /* Remove margin to align with the image */
}

.about-content {
  display: flex; /* Use flexbox for layout */
  flex-direction: row; /* Arrange items in a row */
  justify-content: center; /* Center the content horizontally */
  align-items: flex-start; /* Align items to the top */
  margin: 20px 0; /* Add space above and below the content */
}

.about-image {
  max-width: 220px; /* Make the image smaller */
  height: 270px; /* Adjust height to maintain aspect ratio */
  object-fit: cover; /* Ensure the image fits within its container without distortion */
  margin-right: 30px; /* Space between image and text */
  margin-left: -20px; /* Move the image slightly more to the left */
  border-radius: 10px; /* Slightly round the corners */
}

/* Text styling */
.about-text {
  width: 40%; /* Text takes up 40% of the container */
  max-width: 600px; /* Limit text width for readability */
  line-height: 1.4; /* Improve line spacing */
  color: #ffffff; /* Change text color for contrast */
  text-align: left; /* Align text left for a cleaner look */
  display: flex;
  flex-direction: column; /* Arrange text vertically */
  justify-content: flex-start; /* Align text to the top */
  margin-top: 20px; /* Add space above the text */
}

.about-space {
  height: 20px; /* Adjust the height for space between title and paragraph */
}




/* Portfolio Section ------------------------------------------------------------------------------------------- */
.portfolio-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #141414;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
}

.project-card {
  background-color: #232323;
  padding: 20px;
  border-radius: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
}

.project-card h3 {
  color: #ef0000;
  font-size: 1.4rem;
  margin: 20px 0; /* Combined margin */
}

.project-card p {
  font-size: 1rem;
  color: #eaeaea;
  text-align: left;
}




/* Modal Styling ----------------------------------------------------------------------------------------------- */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.modal-content {
  background-color: #1a1a1a;
  padding: 40px;
  border-radius: 15px;
  max-width: 600px;
  width: 80%;
  text-align: center;
  color: #00ff1e;
  position: relative;
}

.modal-content img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 2rem;
  color: #ef0000;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close-button:hover {
  color: #ff5252;
}




/* Skills Section -------------------------------------------------------------------------------------------- */
.skills-section {
  padding: 80px 20px;
  background-color: #1a1a1a;
  text-align: center; /* Center the text */
}

.skills-section h2 {
  font-family: "Righteous", sans-serif; /* Changed to Righteous for skills heading */
  font-size: 2.8rem;
  color: #ffffff;
  margin-bottom: 20px;
}

.skills-section ul {
  list-style: none;
  display: flex;
  justify-content: center; /* Center the items */
  gap: 30px; /* Space between items */
  flex-wrap: wrap; /* Allow wrapping to the next line */
}

.skills-section li {
  background-color: #232323;
  padding: 15px 25px;
  border-radius: 10px;
  font-size: 1.2rem;
  color: #eaeaea;
  transition: background-color 0.3s ease;
  display: flex; /* Use flexbox for li items */
  align-items: center; /* Center items vertically */
}

/* Style for skill icons */
.skill-icon {
  width: 40px; /* Set desired width */
  height: auto; /* Maintain aspect ratio */
  margin-right: 10px; /* Space between the icon and text */
}





/* Contact Section --------------------------------------------------------------------------------------------- */
.contact-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #141414;
}

.contact-section h2 {
  font-family: "Righteous", sans-serif; /* Changed to Righteous for contact heading */
  font-size: 2.8rem;
  color: #ef0000;
  margin-bottom: 20px;
}

.contact-button {
  background-color: #ef0000;
  color: #fff;
  padding: 15px 30px;
  font-size: 1.2rem;
  border: none;
  border-radius: 50px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.contact-button:hover {
  background-color: #ff5252;
}

/* Footer */
.footer {
  background-color: #0d0d0d;
  color: #eaeaea;
  padding: 30px;
  text-align: center;
}

.footer p {
  font-size: 1rem;
  color: #777777;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 15px;
}

.social-icon {
  color: #ef0000;
  font-size: 1.8rem;
  transition: color 0.3s ease;
}

.social-icon:hover {
  color: #ff5252;
}

/* Testimonials Section */
.testimonials-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #1a1a1a;
}

.testimonials-section h2 {
  font-family: "Righteous", sans-serif; /* Changed to Righteous for testimonials heading */
  font-size: 2.8rem;
  color: #ffffff;
  margin-bottom: 40px;
}

.testimonials-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.testimonial-card {
  background-color: #232323;
  padding: 20px;
  border-radius: 10px;
  max-width: 600px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.testimonial-feedback {
  font-size: 1.2rem;
  font-style: italic;
  color: #eaeaea;
  margin-bottom: 10px;
}

.testimonial-name {
  font-weight: bold;
  color: #ffffff;
}

.testimonial-position {
  color: #a1a1a1;
}
